@font-face {
  font-family: 'SFProText';
  src: url('/fonts/SFPro/sf-pro-text-bold.eot');
  src: url('/fonts/SFPro/sf-pro-text-bold.eot?#iefix') format('embedded-opentype'),
       url('/fonts/SFPro/sf-pro-text-bold.woff') format('woff'),
       url('/fonts/SFPro/sf-pro-text-bold.ttf') format('truetype'),
       url('/fonts/SFPro/sf-pro-text-bold.svg#sf_pro_textbold') format('svg');
  font-weight: 700;
  font-style: normal;
}

